@charset "UTF-8";
/* system
--------------------------- */
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
/* reset
--------------------------- */
/* 
 * destyle.css v1.0.14
 * https://github.com/nicolas-cusan/destyle.css 
 */
* {
  box-sizing: border-box;
}

::after,
::before {
  box-sizing: inherit;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  margin: 0;
}

main {
  display: block;
}

address,
blockquote,
dl,
figure,
form,
iframe,
p,
pre,
table {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  margin: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

dt {
  font-weight: 700;
}

dd {
  margin-left: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  border: 0;
  border-top: 1px solid;
  margin: 0;
  clear: both;
  color: inherit;
}

pre {
  font-family: monospace, monospace;
  font-size: inherit;
}

address {
  font-style: inherit;
}

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  vertical-align: bottom;
}

embed,
iframe,
object {
  border: 0;
  vertical-align: bottom;
}

button,
input,
optgroup,
select,
textarea {
  -webkit-appearance: none;
  appearance: none;
  vertical-align: middle;
  color: inherit;
  font: inherit;
  border: 0;
  background: 0 0;
  padding: 0;
  margin: 0;
  outline: 0;
  border-radius: 0;
  text-align: inherit;
}

[type=checkbox] {
  -webkit-appearance: checkbox;
  appearance: checkbox;
}

[type=radio] {
  -webkit-appearance: radio;
  appearance: radio;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

[type=button],
[type=reset],
[type=submit],
button {
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
}

[type=button][disabled],
[type=reset][disabled],
[type=submit][disabled],
button[disabled] {
  cursor: default;
}

[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner,
button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring,
button:-moz-focusring {
  outline: 1px dotted ButtonText;
}

select::-ms-expand {
  display: none;
}

option {
  padding: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
  min-width: 0;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

label[for] {
  cursor: pointer;
}

details {
  display: block;
}

summary {
  display: list-item;
}

[contenteditable] {
  outline: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: 700;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.pc-only {
  display: none;
}

@media (min-width: 890px) {
  .pc-only {
    display: inline-block;
  }
}
/* base
--------------------------- */
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  color: #333;
  background-color: #fff;
  font-size: 1.6rem;
  font-family: YuGothic, "Yu Gothic", "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-weight: 500;
  max-width: 100vw;
  min-width: 350px;
  width: 100%;
  overflow-x: hidden;
}

::selection {
  background-color: rgba(51, 51, 51, 0.7);
  color: #fff;
}

img,
video,
object {
  max-width: 100%;
  height: auto;
  border: none;
  display: block;
}

a {
  color: #1ca9e3;
  text-decoration: underline;
  position: relative;
}
a:hover {
  text-decoration: none;
}

a:hover img {
  text-decoration: none;
  opacity: 0.8;
  transition: all 0.4s;
}

a,
label,
input,
button {
  transition: all 0.4s;
}

/* l-header
--------------------------- */
.l-header {
  width: 100%;
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 2;
  transition: all 0.4s;
  transition: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 4px -4px rgba(51, 51, 51, 0.6);
  /* menu toggle */
}
.l-header.is-headIn {
  transform: translateY(-100%);
}
.l-header__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-header__logo {
  text-transform: uppercase;
  text-decoration: none;
  color: #333;
  font-size: 2rem;
  font-weight: bold;
}
.l-header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 14px;
}
.l-header__navCta {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  padding: 3px 16px;
  color: #fff;
  background-color: #333;
  border: solid 1px #333;
  border-radius: 50px;
}
.l-header__navCta:hover {
  color: #333;
  background-color: transparent;
  text-decoration: none;
}
.l-header__navLists {
  z-index: 1;
  position: fixed;
  top: 60px;
  left: 0;
  left: 100%;
  width: 100%;
  max-width: 100vw;
  background: #fff;
  text-align: center;
  transition: all 0.4s;
}
.l-header__navLists a {
  color: #333;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  display: inline-block;
  padding: 0 16px;
  margin: 16px auto;
}
.l-header__navLists a::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  content: "";
  width: 0;
  height: 2px;
  background-color: #333;
  transition: 0.4s;
  transform: translateX(-50%);
}
.l-header__navLists a:hover {
  text-decoration: none;
}
.l-header__navLists a:hover:after {
  width: 100%;
}
.l-header__navHamburger {
  display: block;
  position: relative;
  width: 25px;
  height: 40px;
  transition: transform 0.4s ease-in;
  transition: all 0.4s;
}
.l-header__navHamburger > span, .l-header__navHamburger > span:before, .l-header__navHamburger > span:after {
  display: block;
  position: absolute;
  height: 4px;
  width: 25px;
  background-color: #333;
  border-radius: 1px;
  content: "";
  cursor: pointer;
  transition: all 0.4s;
}
.l-header__navHamburger > span {
  top: 47%;
}
.l-header__navHamburger > span:before {
  transform: translateY(-9px);
}
.l-header__navHamburger > span:after {
  transform: translateY(9px);
}
.l-header__navClose {
  display: none;
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: 0.4s ease-in-out;
  transition: all 0.4s;
}
.l-header #l-header__navToggle {
  display: none;
}
.l-header #l-header__navToggle:checked + .l-header__navHamburger {
  transform: rotate(45deg);
}
.l-header #l-header__navToggle:checked + .l-header__navHamburger span:before,
.l-header #l-header__navToggle:checked + .l-header__navHamburger span:after {
  transition: all 0.4s;
  transform: rotate(90deg);
}
.l-header #l-header__navToggle:checked ~ .l-header__navClose {
  display: block;
}
.l-header #l-header__navToggle:checked ~ .l-header__navLists {
  display: block;
  top: 60px;
  left: 0;
}
@media (min-width: 640px) {
  .l-header__inner {
    justify-content: space-evenly;
  }
  .l-header__nav {
    flex-direction: row-reverse;
  }
  .l-header__navCta {
    padding: 3px 32px;
  }
  .l-header__navLists {
    display: flex;
    align-items: center;
    gap: 10px;
    position: relative;
    top: 0;
    left: 0;
    background: transparent;
  }
  .l-header__navLists a {
    margin: 8px auto;
    padding: 0 8px;
  }
  .l-header__navLists a:after {
    background-color: #333;
  }
  .l-header__navHamburger {
    display: none;
  }
}
@media (min-width: 890px) {
  .l-header__inner {
    gap: 200px;
  }
}

.l-footer {
  text-align: center;
  font-size: 1.4rem;
  background: #333;
  padding: 40px 0 50px;
}
.l-footer__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 20px;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  gap: 16px;
}
.l-footer__subtitle {
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 8px;
}
.l-footer__lists li + li {
  margin-top: 6px;
}
.l-footer__lists a {
  color: #fff;
}
.l-footer__copy {
  font-size: 1.2rem;
  margin: 8px auto 0;
}
@media (min-width: 640px) {
  .l-footer__inner {
    flex-direction: row;
  }
  .l-footer__copy {
    margin: auto 0 0;
  }
}
.c-top {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  background-color: rebeccapurple;
}

/* c-loading
--------------------------- */
.c-loading {
  width: 100vw;
  height: 100vh;
  transition: all 1s;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}
.c-loading.is-loaded {
  opacity: 0;
  visibility: hidden;
}
.c-loading__spinner {
  width: 100px;
  height: 100px;
  margin: 40vh auto;
  border: 3px solid #ca6060;
  border-top: 3px solid transparent;
  border-radius: 50%;
  animation: loadingAnimation 0.8s linear 0s infinite;
}
@keyframes loadingAnimation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}
/* c-section
--------------------------- */
.c-section {
  background-color: #fff;
  padding: 120px 0;
}
.c-section:nth-of-type(even) {
  background-color: #efefef;
}
.c-section__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 20px;
}
.c-section__title {
  text-align: center;
  font-size: 2.8rem;
  font-weight: 700;
  margin: 0 auto 12px;
}
.c-section__lead {
  display: table;
  margin: 0 auto 32px;
  text-align: left;
}
.c-section__block {
  margin: 32px auto 0;
}
/* c-table
--------------------------- */
.c-table {
  white-space: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.c-table__block {
  border-collapse: collapse;
  border: solid 3px #333;
  margin: 0 auto;
}
.c-table caption {
  text-align: center;
  color: #fff;
  font-weight: bold;
  background: #333;
  padding: 8px 10px;
}
.c-table th,
.c-table td {
  padding: 8px 10px;
}
.c-table th {
  border: solid 1px #333;
  background-color: rgba(51, 51, 51, 0.2);
}
.c-table td {
  border: dashed 1px #333;
}
/* c-lists
--------------------------- */
.c-lists li {
  position: relative;
  padding-left: 24px;
}
.c-lists li:before {
  position: absolute;
  left: 0;
  content: "\e876";
  font-family: "Material Icons";
  font-weight: 900;
}
.c-listsTitle {
  font-size: 2rem;
  font-weight: bold;
}
/* c-details
--------------------------- */
.c-details {
  width: 100%;
  margin: 0 auto;
  /* アコーディオンオープン時のスタイル */
}
.c-details summary {
  position: relative;
  display: block;
  padding: 10px 20px;
  font-weight: bold;
  background-color: rgba(51, 51, 51, 0.1);
  cursor: pointer;
  transition: 0.2s;
  /* デフォルトの矢印を消す */
  /* アコーディオンの＋アイコン */
}
.c-details summary:hover {
  background-color: rgba(51, 51, 51, 0.2);
}
.c-details summary::-webkit-details-marker {
  display: none;
}
.c-details summary:after {
  position: absolute;
  top: 2px;
  right: 3%;
  content: "\e145";
  font-family: "Material Icons";
  font-size: 28px;
  color: #333333;
  margin: auto;
  transition: 0.4s;
}
.c-details[open] {
  /* アニメーション */
}
.c-details[open] summary {
  background-color: rgba(51, 51, 51, 0.3);
}
.c-details[open] summary:after {
  transform: rotate(135deg);
  /* アイコンを回転 */
}
.c-details[open] .c-faq__content {
  animation: detailsopen 0.5s ease;
}
@keyframes detailsopen {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.c-details__content {
  padding: 10px 20px;
}
@media (min-width: 640px) {
  .c-details {
    width: 80%;
  }
}
/* c-form
--------------------------- */
.c-form {
  max-width: 600px;
  margin: 0 auto;
}
.c-form__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.c-form__label, .c-form__input {
  padding: 10px;
}
.c-form__label {
  width: 90%;
  position: relative;
}
.c-form__input {
  width: 90%;
  font-size: 16px;
  border: solid 1px #333;
  border-radius: 4px;
}
.c-form__input:focus-visible {
  outline: #ca6060 auto 1px;
}
.c-form ul.c-form__input {
  border: none;
  list-style-type: none;
  margin: 0;
}
.c-form textarea.c-form__input {
  height: 160px;
}
.c-form__required {
  position: absolute;
  top: 5px;
  right: 20px;
  color: #efefef;
  background-color: #ca6060;
  border-radius: 2px;
  padding: 5px 5px;
  margin: 0 0 0 18px;
}
.c-form__submit {
  text-align: center;
  margin-top: 36px;
}
.c-form__button {
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
  background-color: #ca6060;
  border: solid 2px #ca6060;
  border-radius: 50px;
  padding: 16px 20px;
  min-width: 300px;
  text-decoration: none;
  position: relative;
}
.c-form__button:after {
  position: absolute;
  right: 24px;
  content: "\e5e1";
  font-family: "Material Icons";
}
.c-form__button:hover {
  color: #ca6060;
  background-color: transparent;
}
.c-formThanks {
  text-align: center;
  max-width: 600px;
  margin: 40px auto 0;
  padding: 8px;
  border: solid 3px #ca6060;
  border-radius: 2px;
  display: none;
}
.c-formThanks.is-received {
  display: block;
}
@media (min-width: 640px) {
  .c-form__item {
    flex-wrap: nowrap;
  }
  .c-form__label {
    width: 40%;
  }
  .c-form__input {
    width: 55%;
  }
}
/* c-cta
--------------------------- */
.c-cta {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.c-cta__copy {
  text-align: center;
  position: relative;
  display: inline-block;
  padding: 0 30px;
}
.c-cta__copy:before, .c-cta__copy:after {
  content: "";
  position: absolute;
  top: 50%;
  display: inline-block;
  width: 28px;
  height: 1px;
  background-color: #333;
}
.c-cta__copy:before {
  left: 0;
  transform: rotate(60deg);
}
.c-cta__copy:after {
  transform: rotate(-60deg);
  right: 0;
}
.c-cta__button {
  display: inline-block;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
  background-color: #ca6060;
  border: solid 2px #ca6060;
  border-radius: 50px;
  margin: 0 auto;
  padding: 16px 20px;
  min-width: 320px;
  max-width: 100%;
  text-decoration: none;
  position: relative;
}
.c-cta__button:after {
  position: absolute;
  right: 24px;
  content: "\e5e1";
  font-family: "Material Icons";
}
.c-cta__button:hover {
  color: #ca6060;
  background-color: transparent;
}
/* p-contact
--------------------------- */
.u-inline-block {
  display: inline-block;
}

